import React, { useState, useEffect } from 'react';
import axios from 'axios';
import BgImg from './../../img/figure/bg22-l.jpg';

const Login = ({ setLoggedIn, setUser }) => {
  const [userName, setUserName] = useState('');
  const [userPassword, setUserPassword] = useState('');
  const [error, setError] = useState(false);

  useEffect(() => {
    const script = document.createElement('script');
    script.src = process.env.PUBLIC_URL + '/js/main.js';
    script.async = true;
    document.body.appendChild(script);
  }, []);

  const onLogin = async (e) => {
    e.preventDefault();
    const data = { userName, userPassword };
    try {
      const res = await axios.post(
        'https://ems-dev.azurewebsites.net/api/Login/Create',
        data
      );
      setError(false);
      setLoggedIn(true);
      setUser(res.data);
    } catch (err) {
      setError(true);
    }
  };

  return (
    <section
      className='fxt-template-animation fxt-template-layout22'
      data-bg-image={process.env.PUBLIC_URL + '/img/figure/bg22-l.jpg'}
    >
      {/* Star Animation Start Here */}
      <div className='star-animation'>
        <div id='stars1' />
        <div id='stars2' />
        <div id='stars3' />
        <div id='stars4' />
        <div id='stars5' />
      </div>
      {/* Star Animation End Here */}
      <div className='container'>
        <div className='row align-items-center'>
          <div className='col-lg-6 col-12 fxt-none-991'></div>
          <div className='col-lg-6 col-12 fxt-bg-color'>
            <div className='fxt-content'>
              <div className='fxt-form'>
                <h2>Login</h2>
                <p>Smart-Fi </p>
                <form onSubmit={onLogin}>
                  {error && (
                    <div class='alert alert-danger' role='alert'>
                      Invalid credentials
                    </div>
                  )}
                  <div className='form-group'>
                    <label htmlFor='email' className='input-label'>
                      User Name
                    </label>
                    <input
                      type='text'
                      id='email'
                      className='form-control'
                      name='email'
                      placeholder=''
                      required='required'
                      value={userName}
                      onChange={(e) => {
                        setUserName(e.target.value);
                      }}
                    />
                  </div>
                  <div className='form-group'>
                    <label htmlFor='password' className='input-label'>
                      Password
                    </label>
                    <input
                      id='password'
                      type='password'
                      className='form-control'
                      name='password'
                      placeholder='********'
                      required='required'
                      value={userPassword}
                      onChange={(e) => {
                        setUserPassword(e.target.value);
                      }}
                    />
                    <i
                      toggle='#password'
                      className='fa fa-fw fa-eye toggle-password field-icon'
                    />
                  </div>
                  <div className='form-group'>
                    <div className='fxt-checkbox-area'>
                      <div className='checkbox'>
                        <input id='checkbox1' type='checkbox' />
                        <label htmlFor='checkbox1'>Keep me logged in</label>
                      </div>
                      <a href='#' className='switcher-text'>
                        Forgot Password
                      </a>
                    </div>
                  </div>
                  <div className='form-group'>
                    <button type='submit' className='fxt-btn-fill'>
                      Log in
                    </button>
                  </div>
                </form>
              </div>
              <div className='fxt-style-line'>
                <h3>Follow us on Social Media</h3>
              </div>
              <ul className='fxt-socials'>
                <li className='fxt-facebook'>
                  <a href='#' title='Facebook'>
                    Twitter
                  </a>
                </li>
                <li className='fxt-twitter'>
                  <a href='#' title='twitter'>
                    Facebook
                  </a>
                </li>
                <li className='fxt-linkedin'>
                  <a href='#' title='linkedin'>
                    Linkedin
                  </a>
                </li>
              </ul>
              <div className='fxt-footer'>
                <p>
                  Don't have an account?
                  <a href='#' className='switcher-text'>
                    Contact Administrator
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Login;
